

.main{
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 40px;
}

.menu{
  width: 100%;
  display: flex;
  gap: 30px;
  align-items: center;
  & > li{
    position: relative;
    display: flex;
    align-items: center;
    gap: 5px;
    padding: 20px 0;
    & > img{
      display: block;
      width: 18px;
      transition: transform .3s ease;
    }
    a{
      color: #737582;
      font-size: 16px;
      font-weight: 500;
      transition: color .3s ease;
    }
    .submenu{
      position: absolute;
      z-index: 2;
      top: 100%;
      left: 0;
      visibility: hidden;
      opacity: 0;
      transition: all .3s ease;
      display: flex;
      flex-direction: column;
      gap: 10px;
      padding: 10px 0;
      border-radius: 8px;
      width: max-content;
      background: #172734;
      li{
        transition: all .3s ease;
        padding: 10px;
        a{
          display: block;
          width: 100%;
        }
        &:hover{
          background: rgba(255,255,255,.2);
        }
      }
    }
    &:hover{
      & > a{
        color: #4172f6;
      }
      & > img{
        transform: rotateX(180deg);
      }
      .submenu{
        visibility: visible;
        opacity: 1;
      }
    }

  }
}

.actions{
  display: flex;
  align-items: center;
  gap: 10px;
  &-btns{
    display: flex;
    align-items: center;
    gap: 20px;
    color: #4172f6;
    &>*{
      width: 20px;
    }
  }
  .login{
    color: #4172f6;
    font-size: 16px;
    border: 1px solid #4172f6;
    padding: 8px 15px;
    border-radius: 8px;
    transition: all .3s ease;
    &:hover{
      background: #4172f6;
      color: #fff;
    }
  }
}

.langs{
  background: var(--background-color);
  padding: 30px;
  border: 8px;
  color: #737582;
  display: flex;
  flex-direction: column;
  gap: 20px;
  border-radius: 8px;
  box-shadow: 0px 30px 50px rgba(81,60,255,.1);
  position: relative;
  &_btn{
    position: absolute;
    top: 10px;
    right: 20px;
  }
  h2{
    font-weight: 700;
    font-size: 20px;
  }
  &_items{
    display: grid;
    gap: 20px;
    grid-template-columns: repeat(2,1fr);
  }
  &_item{
    cursor: pointer;
    border-radius: 8px;
    display: flex;
    align-items: center;
    gap: 10px;
    padding: 10px;
    transition: all .3s ease;
    &:hover{
      color: #fff;
      background: hsla(0,1%,41%,.4);
    }
  }
}