
.main{
    padding-top: 50px;
}
.toggle{
    display: flex;
    align-items: center;
    gap: 20px;
    &_btns{
        display: flex;
        align-items: center;
        gap: 10px;
        padding: 10px;
        border-radius: 8px;
        color: #fff;
        background: var(--tab-color);
        border:1px solid var(--select-border-color);
        button{
            padding: 10px 40px;
            border-radius: 8px;
        }
        button:first-child{
            &.active{
                background: #64cb8c;
            }
        }
        button:last-child{
            &.active{
                background: #ed4f5c;
            }
        }
    }
    h2{
        color: var(--accent-color);
        font-size: 20px;
        text-decoration: underline;
    }
}

.filter{
    &_form{
        margin-top: 20px;
        display: flex;
        align-items: flex-end;
        gap: 20px;
        position: relative;
    }
    &_input{
        width: 250px;
        position: relative;
        display: flex;
        flex-direction: column;
        gap: 10px;
        label{
            font-size: 14px;
            color: #fff;
        }
        &:hover{
            & > input{
                border-color: var(--accent-color);
            }
            .currency_select{
                color: var(--banner-title-color);
            }
        }
        input{
            cursor: pointer;
            padding: 10px;
            border-radius: 6px;
            border:1px solid var(--select-border-color);
            transition: all .3s ease;
            color: var(--banner-title-color);
            &::placeholder{
                color: var(--banner-text-color);
            }
        }
    }
}

.currency_select{
    user-select: none;
    cursor: pointer;
    position: absolute;
    bottom: 15%;
    right: 5px;
    color:var(--select-border-color);
    display: flex;
    align-items: center;
    gap: 5px;
    h3{
        transition: all .3s ease;
    }
    &>img{
        transition: all .3s ease;
        width: 18px;
    }

}

.currency_toggle{
    opacity: 0;
    visibility: hidden;
    position: absolute;
    z-index: 2;
    top: 100%;
    left: 0;
    width: 100%;
    padding: 10px;
    border-radius: 8px;
    display: flex;
    flex-direction: column;
    gap: 10px;
    background: var(--sum-popup);
    transition: all .3s ease;
    input{
        width: 100%;
    }
    &.active{
        visibility: visible;
        opacity: 1;
    }
}

.currency_items{
    display: flex;
    flex-direction: column;
    gap: 10px;
}
.currency_item{
    cursor: pointer;
    padding: 10px;
    border-radius: 8px;
    display: flex;
    align-items: center;
    gap: 5px;
    color: var(--banner-text-color);
    transition: all .3s ease;
    &:hover{
        background:rgba(255,255,255,.2) ;
    }
}

.currency_input{
    position: relative;
    img{
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        right: 10px;
    }
}

.paid_toggle,.filter_toggle{
    position: absolute;
    top: 100%;
    left: 0;
    width: 100%;
    visibility: hidden;
    opacity: 0;
    transition: all .3s ease;
    background: var(--sum-popup);
    padding: 10px;
    border-radius: 8px;

}
.filter_toggle{
    width: max-content;
}

.paid_items,.filter_items{
    display: flex;
    flex-direction: column;
    gap: 10px;
}



.paid_item,.filter_item{
    user-select: none;
    cursor: pointer;
    color: var(--banner-text-color);
    transition: all .3s ease;
    &:hover{
        color: var(--accent-color);
    }
}
.paid_item{
    display: flex;
    align-items: center;
    gap: 10px;
    span{
        display: block;
        width: 15px;
        height: 15px;
        border-radius: 50%;
        background: var(--bank-tag);
    }
    &.active{
        color: var(--accent-color);
    }
}

.filter_img{
    position: absolute;
    top: 55%;
    transform: translateY(-50%);
    right: 10px;
    width: 18px;
    img{
        transition: all .3s ease;
    }
    &.active{
        img{
            width: 100%;
            transform: rotate(180deg);
        }
    }
}

.filter_input:last-child{
    width: 100px;
}

.filter_btn{
    display: flex;
    align-items: center;
    gap: 10px;
    background: var(--accent-color);
    height: 100%;
    padding: 10px 40px;
    border-radius: 8px;
    color: var(--light);
    font-weight: 600;
}

.table_names{
    display: grid;
    grid-template-columns: repeat(auto-fit,minmax(calc((100% - 165px)/5),1fr));
}

.items{
    display: flex;
    flex-direction: column;
    gap: 10px;
}

.item{
    display: grid;
    grid-template-columns: repeat(auto-fit,minmax(calc((100% - 135px)/5),1fr));
    gap: 30px;
    padding: 50px 0;
    align-items: center;
    border-top: 1px solid var(--text-color);
    color: var(--text-color);
    &_info{
        display: flex;
        gap: 12px;
    }
    &_img{
        position: relative;
        height: fit-content;

        img{
            min-height: 40px;
            min-width: 40px;
            height: 40px;
            width: 40px;
            border-radius: 40px;
            background-color: #64cb8c;
            object-fit: cover;
        }
        span{
            display: block;
            position: absolute;
            top: -10%;
            background: #64cb8c;
            right: -15%;
            width: 15px;
            height: 15px;
            border-radius: 50%;
            border: 3px solid var(--background-color);
            &:last-child{
                top: auto;
                bottom: -10%;
                background: var(--accent-color);
            }
        }
    }
    &_info__content{
        display: flex;
        flex-direction: column;
        gap: 5px;
        color: var(--text-color);
    }
}
.item_name{
    font-weight: 700;
    font-size: 16px;
}
.item_active{
    color: #64cb8c;
    font-size: 14px;
}
.item_stat{
    font-size: 14px;
}

.item_btns{
    padding-top: 20px;
    display: flex;
    justify-content: space-around;
    align-items: center;
    &>*{
        display: flex;
        gap: 15px;
        align-items: center;
    }
}

.item_price{
    display: flex;
    align-items: center;
    gap: 5px;
    text-transform: uppercase;
    font-weight: 600;
    h3{
        opacity: .5;
    }
}
.item_limit{
    width: 80%;
    display: flex;
    flex-direction: column;
    gap: 10px;
    font-weight: 600;
    font-size: 12px;
    &>*{
        display: grid;
        grid-template-columns: 1fr 1fr;
        align-items: center;
        gap: 5px;
        &>*{
            &:first-child{
                opacity: .5;
            }
            display: flex;
            align-items: center;
            gap: 5px;
            width: max-content;
        }
    }

}

.item_paid{
    button{
        background: var(--bank-tag);
        border-radius: 5px;
        padding: 5px 10px;
        height: fit-content;
        cursor: pointer;
        font-style: normal;
        font-weight: 500;
        font-size: 16px;
    }
}
.item_btn{
    width: max-content;
    background: var(--accent-color);
    color: var(--light);
    font-weight: 700;
    padding: 10px 30px;
    border-radius: 8px;
}
.table_names{
    padding: 40px 0;
    & > div {
        h2{
            color: var(--text-color);
        }
        &:nth-child(2){
            h2 span{
                padding: 5px;
                border-radius: 10px;
                color:#f59b13 ;
                background: rgba(245,155,19,.1);
            }
        }
        &:last-child{
            h2 span{
                padding: 5px;
                border-radius: 10px;
                color: #64cb8c;
                background: rgba(100,203,140,.1);
            }
        }
    }
}