@import "the-new-css-reset/css/reset.css";
@import 'reactjs-popup/dist/index.css';

*,
*::after,
*::before{
  box-sizing: border-box;
}

:root{
  --light:#fff;
  --dark:#000;
  --background-color: #0f151d;
  --banner-title-color: #abbfd4;
  --banner-text-color: rgba(171,191,212,.7);
  --accent-color: #4172f6;
  --tab-color: #131a26;
  --select-border-color: #323d4d;
  --sum-popup: #1d2532;
  --text-color: #abbfd4;
  --bank-tag:#323d4d;
}

body,html{
  font-family: 'Roboto Flex', sans-serif;
}

body{
  background: var(--background-color);
}

.ibg{
  position: relative;
  img{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

.container{
  max-width: 1470px;
  margin: 0 auto;
  padding: 0 15px;
}

button{
  cursor: pointer;
}

.popup-content {
  width: 40% !important;
  margin: auto !important;
  padding: 0 !important;
  border: 0 !important;
  background: transparent !important;
}

.popup-overlay{
  background: hsla(0,1%,41%,.4) !important;
}

.stock_currency__toggle{
  opacity: 0;
  visibility: hidden;
  position: absolute;
  z-index: 2;
  top: 100%;
  left: 0;
  width: 100%;
  padding: 10px;
  border-radius: 8px;
  display: flex;
  flex-direction: column;
  gap: 10px;
  background: var(--sum-popup);
  transition: all .3s ease;
  input{
    width: 100%;
  }
  &.active{
    visibility: visible;
    opacity: 1;
  }
}

.stock_paid__toggle,.stock_filter__toggle{
  &.active{
    opacity: 1;
    visibility: visible;
  }
}

.stock_currency__select{
  &.active{
    img:last-child{
      transform: rotate(180deg);
    }
  }
}


#work{
  padding-top: 100px;
}